.forms {
  padding: 0 20px;
}

.forms__invitation-text {
  font-family: $font-stack;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #283645;
  text-align: center;
}

.forms__explanation {
  font-family: $font-stack;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
  color: #283645;
}

@media (min-width: $desktop-width) {
  .forms__wrapper {
    padding: 0 132px;
  }

  .forms {
    padding: 0;
  }
}
