.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
}

.site-list__link {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
}

.site-list__item {
  background-color: #283645;
  width: 100%;
  text-align: center;
  padding: 22px 18px;
  box-shadow: 0px 1px 0px #000000, inset 0px 1px 0px rgb(255 255 255 / 15%);
}

@media (min-width: $desktop-width) {
  .site-list {
    flex-direction: row;
    min-width: 750px;
    padding: 0;
  }

  .site-list__item {
    padding: 0;
    background-color: rgba(0, 0, 0, 0.0);
    box-shadow: none;
  }

  .site-list__item:first-child {
    display: none;
  }

  .site-list__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-transform: lowercase;
  }
}

